import HttpService from './HttpService';
import Cookies from 'js-cookie'



export const RegisterService = (credentials) =>{
    const http = new HttpService();
    let addedUrl = "admin/register";
    let tokenId = 'vt-music';
    return http.postData(credentials,addedUrl,"POST",tokenId).then(data=>{
        console.log(data);
        console.log(JSON.stringify(data));
        return data;
    }).catch((error)=> {console.log(error)
        return error; 
         });
}

export const AdminListService = (perPage,pageIndex) =>{
    const http = new HttpService();
    let addedUrl = "admin/admin-list/"+perPage+"?page="+pageIndex;
    let tokenId = 'vt-music';
    return http.getData(addedUrl,tokenId).then((data)=>
    {
        console.log(data)
        return data;

    }).catch((error)=>
    {
        console.log(error)
        return error;
    });
}


