import { 
    RESTART_REGISTRATION_RESPONSE,
     REGISTRATION_LOADING, 
     REGISTRATION_SUCCESS,
     REGISTRATION_ERROR,
     REGISTRATION_CODE_ERROR,
     RESTART_ADMINLIST_RESPONSE,
     ADMINLIST_LOADING,
     ADMINLIST_SUCCESS,
     ADMINLIST_ERROR,
     ADMINLIST_CODE_ERROR,
     RESTART_CHANGE_PASSWORD_RESPONSE,
     CHANGE_PASSWORD_LOADING,
     CHANGE_PASSWORD_SUCCESS,
     CHANGE_PASSWORD_ERROR,
     CHANGE_PASSWORD_CODE_ERROR
    } from "../ActionTypes/AdminProfileTypes";
import { 
    RegisterService,
     AdminListService, 
     ChangePasswordService
    } from "../../services/AdminProfileService";



 
export const RegisterNewAdminAction = (credentials) =>
{
   return (dispatch)=>{
 
       dispatch({type:RESTART_REGISTRATION_RESPONSE});
       dispatch({type:REGISTRATION_LOADING});

    RegisterService(credentials).then((res)=>{

               console.log(res);

               if(res.hasOwnProperty('success') && res.success==true){
                dispatch({type:REGISTRATION_SUCCESS,res});
              

               }else if(res.hasOwnProperty('success') && res.success==false) {
                    dispatch({type:REGISTRATION_ERROR,res});  
               }
           },
           error=>{
            dispatch({type:REGISTRATION_CODE_ERROR,error});
               console.log(error);
           }
       )
   } 

}

export const clearNewAdminRegistrationState = ()=>
{
   return (dispatch)=>
   {
       dispatch({type:RESTART_REGISTRATION_RESPONSE});
   }
}


