import React,{useState,useEffect} from 'react'
import CIcon from '@coreui/icons-react'
import {
  CButton,
  CCol,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow,
  CAlert,
 
} from '@coreui/react'
import { Formik } from 'formik';
import {
    useFormFields,
    disableSubmitButton
 }  from '../../../helpers/hooksFormInput'
 import {
     cleanUpData,
     instantiateAbort
    } from '../../../helpers/componentHelperFunc'

    import {
        useDispatch,
         useSelector
        } from 'react-redux';
        
    import '../styles.css'

import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import Loader from 'react-loader-spinner'
import { useHistory,useParams } from 'react-router-dom';
import { 
  ChangePasswordAction, 
  clearChangePasswordState 
} from '../../../store/Actions/AdminProfileActions';


export default function ChangePasswordForm() {

  
      const [disable, setDisable] = useState(true);

      const dispatch =  useDispatch();
       
      const authResponse = useSelector(state => state.adminProfile.changePasswordResponse);

      const abortEffect = instantiateAbort();


      const [fields,setValues,handleFieldChange] = useFormFields({
          password: "",
          confirm: "",
      
        }); 

const validation = () =>
{
 const errors = {};
  
errors.password =  !fields.password? 'Password Field is Required': '';
errors.confirm =  !fields.confirm? 'Confirm Password Field is Required': '';  
errors.confirm = fields.password != fields.confirm? 'Password and Confirm password do not match':'';

 return errors;
}



 /**
   * ensure button remains disable until all form fields are filled
   */
  useEffect(() => 
  {
    disableButtonIfFieldsAreEmpty(fields);

    return function cleanup()
      {
        cleanUpData(abortEffect);
      }

   }, [fields]); 

const ChangePassword = (e) =>
{
  e.preventDefault();
  dispatch(ChangePasswordAction(fields));
}  


const disableButtonIfFieldsAreEmpty = (dataObject)=>
{
 disableSubmitButton(dataObject,setDisable);
}


const successMessage = (message) =>
{
    console.log(message);
    return(
      <CAlert
      color="success"
      closeButton
      onClick={closeBtnAction}
    >
     {message}
    </CAlert>
  );
    
  
}

 
const displayErrorMessages = (error,message)=>
{
    
    if(typeof error === "undefined")
    {
        return(
            <CAlert
            color="danger"
            closeButton
            onClick={closeBtnAction}
          >
           {message}
          </CAlert>
        );
    }
    
   return  Object.keys(error).map((keys,index)=>{
      
        return(
            <CAlert
            color="danger"
            closeButton
            onClick={closeBtnAction}
            key={keys}
          >
          {error[keys][0]}
          </CAlert>
        );
     
      });

}

 


  const closeBtnAction = () =>
  {
      dispatch(clearChangePasswordState());
  }





    return (
        <Formik
      initialValues={fields}
      validate={validation}
    >
      {({
        errors,
        touched,
        handleBlur,
        /* and other goodies */
      }) => (

        <CForm  onSubmit={ChangePassword}>

       
    {
      /**
       * if the value of the state is loading
       */
      authResponse!="" && authResponse == "loading"?
      <>
	<Loader type="ThreeDots" color="#321fdb" height={60} width={60} />
      </>
      : 
      /**
       * if authResponse.success is true show success message
       */
    authResponse!="" && authResponse.success==true? 
    successMessage(authResponse.message)
    /**
     * else if authResponse.success == false show error messages
     */
       : 
      authResponse.success==false ?
      displayErrorMessages(authResponse.error,authResponse.message) 
      : authResponse

    }

        <h3>Change Password</h3>
        <p className="text-muted">Change Your Password</p>

     

        <CInputGroup className="mb-3">
          <CInputGroupPrepend>
            <CInputGroupText>
              <CIcon name="cil-user" />
            </CInputGroupText>
          </CInputGroupPrepend>
          <CInput type="password"
           id="password"
           name="password"
           value = {fields.password}
           onChange={handleFieldChange}
           onBlur={handleBlur}
          placeholder="Password" autoComplete="password" />
         
         <div className="error_form_response">
          {
            errors.password && touched.password && errors.password
          }
        </div>
       
        </CInputGroup>
  
       

        <CInputGroup className="mb-4">
          <CInputGroupPrepend>
            <CInputGroupText>
              <CIcon name="cil-lock-locked" />
            </CInputGroupText>
          </CInputGroupPrepend>
          <CInput type="password"
           id="confirm"
           value = {fields.confirm}
           onChange={handleFieldChange}
           onBlur={handleBlur}
          placeholder="Confirm Password" autoComplete="current-password" />

<div className="error_form_response">
          {
            errors.confirm && touched.confirm && errors.confirm
          }
        </div>

        </CInputGroup>
       
        

        <CRow>
          <CCol xs="6">
            <CButton color="primary" 
             disabled={disable}
            type="submit" className="px-4">Change Password</CButton>
          </CCol>
         
        </CRow>

      </CForm>

)}

</Formik>

    )
}
